.product {
  display: flex;
  width: 800px;
  border: 1.5px solid rgb(230, 223, 223);
  margin: auto;
  padding: 0.3rem 0.3rem;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}

.product-text {
  font-size: 1.3rem;
  margin-left: 2rem;
  font-weight: bold;
}

.product-label {
  color: rgb(214, 51, 51);
  margin-right: 0.3rem;
}

.btn-dark:hover {
  background-color: rgb(131, 12, 12);
  border-color: rgb(131, 12, 12);
}
